<template>
	<div class="page-box">
		<p class="title">您好，请登录</p>
		<div class="center-box">
			<input type="text" v-model="form.email" placeholder="电子邮箱" />
			<input type="password" v-model="form.password" placeholder="密码" />
			<van-button type="info" block @click="landing">登陆</van-button>
			<div class="link-box">
				<router-link class="link" to="/forget">忘记密码</router-link>
				<router-link class="link" to="/register">新用户注册</router-link>
			</div>
		</div>
		<div class="bottom">
			<!-- <div class="checkbox"><van-checkbox v-model="form.agree" shape="square" icon-size="16px"></van-checkbox></div>
			<p>
				我已阅读并同意维保一点通
				<span class="link">《隐私政策》</span>
				和
				<span class="link">《用户协议》</span>
			</p> -->
		</div>
	</div>
</template>

<script>
export default {
	name: '',
	data() {
		return {
			form: {
				email: '',
				password: '',
				agree: false
			}
		};
	},
	created() {},
	mounted() {},
	computed: {},
	methods: {
		landing() {
			let reg = /\w[-\w.+]*@([A-Za-z0-9][-A-Za-z0-9]+\.)+[A-Za-z]{2,14}/;
			if(this.form.email == ''){
				this.$toast.fail('请填写邮箱');
				return
			}else if(this.form.password == ''){
				this.$toast.fail('请填写密码');
				return
			}else{
				if(!reg.test(this.form.email)){
					this.$toast.fail('请输入正确的邮箱地址');
					return
				}
			}
			this.$router.push('/study');
		}
	},
	components: {},
	beforeDestroy() {},
	watch: {}
};
</script>

<style scoped lang="scss">
.page-box {
	width: calc(100% - 30px);
	height: calc(100% - 100px);
	padding: 45px 15px 55px 15px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	background-color: #fff;
	overflow: auto;

	.title {
		width: 100%;
		font-size: 25px;
	}

	.link {
		color: #169bd5;
	}

	.center-box {
		width: 100%;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: center;

		input {
			width: calc(100% - 5px);
			border-left: none;
			border-right: none;
			border-top: none;
			border-color: #a8a8a8;
			font-size: 15px;
			margin-bottom: 40px;
			padding: 0 0 5px 5px;
		}

		::v-deep {
			.van-button {
				width: calc(100% - 30px);
				border-radius: 6px;
				background-color: #169bd5;
				margin: 30px 0 30px 0;
			}

			.van-button__text {
				font-size: 18px;
			}
		}

		.link-box {
			width: 100%;
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
	}

	.bottom {
		width: 100%;
		display: flex;
		justify-content: center;
		align-items: flex-start;

		.checkbox {
			width: 18px;
		}

		p {
			font-size: 13px;
			margin-left: 5px;
		}
	}
}
</style>
