	computed: {},
	methods: {
		toPath(row) {
			if (row.path) {
				this.$router.push(row.path);
			} else {
				if (row.title == '题库更新') {
					this.updateShow = true;
				}
			}
		},
		closeTips() {
			this.updateShow = false;
		}
	},
	components: {},
	beforeDestroy() {},
