<template>
	<!-- 注册 -->
	<div class="page-box">
		<p class="title">注册</p>
		<div class="center-box">
			<input type="text" v-model="form.email" placeholder="请输入您的邮箱地址" />
			<div class="input-cell">
				<input type="password" v-model="form.code" placeholder="验证码" />
				<span class="link leftText">获取验证码</span>
			</div>
			<div class="tips-input-cell">
				<input type="password" v-model="form.password1" placeholder="设置密码" />
				<span class="tipsText">密码要求8-16位，至少包含数字、字母、符号两种元素 </span>
			</div>
			<input type="password" v-model="form.password2" placeholder="重复该密码" />
			<van-button type="info" block @click="register">注册</van-button>
		</div>
		<div class="bottom">
			<div class="checkbox"><van-checkbox v-model="form.agree" shape="square" icon-size="16px"></van-checkbox></div>
			<p>
				我已阅读并同意维保一点通
				<span class="link">《隐私政策》</span>
				和
				<span class="link">《用户协议》</span>
			</p>
		</div>
	</div>
</template>

<script>
export default {
	name: '',
	data() {
		return {
			form: {
				email: '',
				password1: '',
				password2: '',
				code: '',
				agree: false,
			}
		};
	},
	created() {},
	mounted() {},
	computed: {},
	methods: {
		register(){
			let pawReg = /^(?!.[^A-Za-z0-9])(?![a-z]*$)(?![A-Z]*$)(?![0-9]*$)(?![\u4E00-\u9FA5]*$).{8,16}$/
			let reg = /\w[-\w.+]*@([A-Za-z0-9][-A-Za-z0-9]+\.)+[A-Za-z]{2,14}/;
			if(this.form.email == ''){
				this.$toast.fail('请填写邮箱');
				return
			}else if(this.form.code == ''){
				this.$toast.fail('请填写验证码');
				return
			}else if(this.form.password1 == ''){
				this.$toast.fail('请填写密码');
				return
			}else if(this.form.password2 == ''){
				this.$toast.fail('请填写重复密码');
				return
			}else{
				if(!reg.test(this.form.email)){
					this.$toast.fail('请输入正确的邮箱地址');
					return
				}else if(this.form.password1 != this.form.password2){
					this.$toast.fail('两次密码不一致');
					return
				}else if(!pawReg.test(this.form.password1)){
					this.$toast.fail('密码要求8-16位，至少包含数字、字母、符号两种元素');
					return
				}
			}
			this.$router.push('/login');
		}
	},
	components: {},
	beforeDestroy() {},
	watch: {}
};
</script>

<style scoped lang="scss">
.page-box {
	width: calc(100% - 30px);
	height: calc(100% - 55px);
	padding: 45px 15px 10px 15px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	background-color: #fff;
	overflow: auto;

	.title {
		width: 100%;
		font-size: 25px;
	}

	.link {
		color: #169bd5;
	}

	.center-box {
		width: 100%;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: center;
		
		::v-deep {
			.van-button {
				width: calc(100% - 30px);
				border-radius: 6px;
				background-color: #169bd5;
				margin: 30px 0 30px 0;
			}

			.van-button__text {
				font-size: 18px;
			}
		}

		input {
			width: calc(100% - 5px);
			border-left: none;
			border-right: none;
			border-top: none;
			border-color: #a8a8a8;
			margin-bottom: 50px;
			padding: 0 0 5px 5px;
			font-size: 15px;
		}

		.input-cell{
			width: 100%;
			border-bottom: 2px solid #a8a8a8;
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin-bottom:50px;
			
			input{
				border: none;
				margin: 0;
			}
			
			.leftText{
				white-space: nowrap;
				font-size: 15px;
			}
		}
		
		.tips-input-cell{
			width: 100%;
			margin-bottom: 50px;
			
			input{
				margin: 0 0 5px 0;
			}
			
			.tipsText{
				font-size: 12px;
				color: #a8a8a8;
				margin-left: 5px;
			}
		}
	}

	.bottom {
		width: 100%;
		display: flex;
		justify-content: center;
		align-items: flex-start;

		.checkbox {
			width: 18px;
		}

		p {
			font-size: 13px;
			margin-left: 5px;
		}
	}
}
</style>
