<template>
	<!-- 登陆、注册、忘记密码、重置密码、个人信息、设置、查询、考试、理论学习、我的 -->
	<div id="app">
		<van-nav-bar v-show="menuShow" :title="titleShow ? pageTitle : ''" left-text="" right-text="" left-arrow @click-left="onClickLeft" />
		<div :class="className"><router-view></router-view></div>
		<van-tabbar route v-show="tabbarShow">
			<van-tabbar-item replace to="/study">
				<template #icon="props">
					<img :src="props.active ? require('./assets/img/icon/tabbar1A.png') : require('./assets/img/icon/tabbar1.png')" />
				</template>
				学习
			</van-tabbar-item>
			<van-tabbar-item replace to="/examination">
				<template #icon="props">
					<img :src="props.active ? require('./assets/img/icon/tabbar2A.png') : require('./assets/img/icon/tabbar2.png')" />
				</template>
				考试
			</van-tabbar-item>
			<van-tabbar-item replace to="/query">
				<template #icon="props">
					<img :src="props.active ? require('./assets/img/icon/tabbar3A.png') : require('./assets/img/icon/tabbar3.png')" />
				</template>
				查询
			</van-tabbar-item>
			<van-tabbar-item replace to="/my">
				<template #icon="props">
					<img :src="props.active ? require('./assets/img/icon/tabbar4A.png') : require('./assets/img/icon/tabbar4.png')" />
				</template>
				我的
			</van-tabbar-item>
		</van-tabbar>
	</div>
</template>

<script>
export default {
	name: 'app',
	data() {
		return {
			menuShow: false, //导航栏是否显示
			tabbarShow: false, //标签栏是否显示
			titleShow: false, //导航栏标题是否显示
			pageTitle: '' //页面标题
		};
	},
	created() {
		var router = this.$route;
		this.menuShow = router.meta.menuShow;
		this.tabbarShow = router.meta.tabbarShow;
		this.titleShow = router.meta.titleShow;
		this.pageTitle = router.meta.title;
	},
	computed: {
		className() {
			let className = 'viewBox';
			if (this.menuShow) {
				className += ' height-s';
			}
			if (this.tabbarShow) {
				className += ' height-xs';
			}

			return className;
		}
	},
	methods: {
		onClickLeft() {
			this.$router.go(-1);
		}
	},
	components: {},
	watch: {
		$route(to, from) {
			// console.log(to);
			// console.log(from);
			this.menuShow = to.meta.menuShow;
			this.tabbarShow = to.meta.tabbarShow;
			this.titleShow = to.meta.titleShow;
			this.pageTitle = to.meta.title;
		}
	}
};
</script>

<!-- 公共样式 -->
<style lang="scss">
html,
body {
	width: 100%;
	height: 100%;
	padding: 0;
	margin: 0;
}

#app {
	font-family: Avenir, Helvetica, Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	// color: #2c3e50;
	width: 100%;
	height: 100%;
	overflow: hidden;
	background-color: #f8f7fc;
}

p {
	padding: 0;
	margin: 0;
}

a,
input,
button {
	outline: none;
}

.pointer {
	cursor: pointer;
}
/* 单行文本超出隐藏 */
.text-overflow {
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}
/* 多行文本超出隐藏 */
.texts-overflow {
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 3;
	overflow: hidden;
}

/* 消除浏览器输入框自动填充的背景色 */
input:-webkit-autofill,
textarea:-webkit-autofill,
select:-webkit-autofill {
	box-shadow: inset 0 0 0 1000px #fff;
}

// 更改输入框默认提示颜色
input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
	color: #8a8a8a;
}

input:-moz-placeholder,
textarea:-moz-placeholder {
	color: #8a8a8a;
}

input::-moz-placeholder,
textarea::-moz-placeholder {
	color: #8a8a8a;
}

input:-ms-input-placeholder,
textarea:-ms-input-placeholder {
	color: #8a8a8a;
}

// 禁止文本域缩放
textarea {
	resize: none !important;
}
</style>

<style scoped="scoped" lang="scss">
#app {
	::v-deep {
		.van-icon-arrow-left {
			font-size: 20px;
			font-weight: 600;
			color: #000;
		}

		.van-nav-bar__title {
			font-size: 18px;
		}
	}

	.viewBox {
		width: 100%;
		height: 100%;
	}

	.height-s {
		height: calc(100% - 46px);
	}

	.height-xs {
		height: calc(100% - 50px);
	}

	.height-s.height-xs {
		height: calc(100% - 96px);
	}
}
</style>
