import Vue from "vue";
import Axios from "axios"
import qs from 'qs'

var toast = null;

const axios = Axios.create({
	// baseURL: Vue.prototype.constData.baseURL, // 公共地址
	baseURL: '/lw/paas',
	headers: {
		'Content-Type': 'application/json'
		// 'Content-Type' : "application/x-www-form-urlencoded; charset=UTF-8"
	},
	withCredentials: true,
	timeout: 40000,
});

// 添加请求拦截器
axios.interceptors.request.use(function(config) {
	// console.log(config)
	if (toast != null) {
		toast.close(); //关闭加载动画
	}
	toast = null;
	// 在发送请求之前做些什么
	// 设置请求头
	var tinfo = '';
	if (config.url != '/paas/oauth2/token') {
		tinfo = localStorage.getItem('token');
	}
	if (tinfo != null && tinfo != '' && tinfo != undefined) {
		config.headers['Authorization'] = "Bearer " + tinfo;
	}
	if (config.method === 'get') {
		config.params ? config.params : config.params = {};

		if (config.params.hasOwnProperty('responseType')) {
			config['responseType'] = config.params.responseType;
			delete config.params.responseType;
		}

		if (config.params.loading === undefined || config.params.loading) {
			toast = Vue.prototype.$loading({
				lock: true,
				text: 'Loading',
				spinner: 'el-icon-loading',
				background: 'rgba(0, 0, 0, 0.7)'
			});
		}
		//如果data中包含加载动画,从data中删除加载动画字段'loading'
		delete config.params.loading;
	} else {
		//判断请求是否包含不显示加载动画字段
		config.data ? config.data : config.data = {};
		if (config.data.loading === undefined || config.data.loading) {
			toast = Vue.prototype.$loading({
				lock: true,
				text: 'Loading',
				spinner: 'el-icon-loading',
				background: 'rgba(0, 0, 0, 0.7)'
			});
		}

		//如果data中包含加载动画,从data中删除加载动画字段'loading'
		delete config.data.loading;
	}

	if (!(config.data instanceof FormData)) {
		config.data ? config.data : config.data = {};
		if (config.method !== 'get') {
			for (let i in config.data) {
				if (config.data[i] || config.data[i] === 0 || config.data[i] === -1) {
					//删除上传字段中的空格
					let str = typeof config.data[i] === "string" ? config.data[i].replace(/-/g, "/") : config
						.data[i];
					if (typeof config.data[i] === "string" && isNaN(Date.parse(str))) {
						config.data[i] = config.data[i].toString().replace(/\s/g, "");
					}
				} else {
					//删除值为空字段
					delete config.data[i]
				}
			}
			config.data = config.data;
			// config.data = qs.stringify(config.data);
		}
	}

	return config;
}, function(error) {
	// alert('2'+JSON.stringify(error))
	// 对请求错误做些什么
	if (toast != null) {
		toast.close(); //关闭加载动画
	}

	// console.log(error)
	Vue.prototype.$message({
		showClose: true,
		message: '请求失败',
		type: 'error'
	});
	return Promise.reject(error)
})

// 添加响应拦截器
axios.interceptors.response.use(function(response) {
	// alert('3'+JSON.stringify(response))
	// 对响应数据做点什么  
	if (toast != null) {
		toast.close(); //关闭加载动画
	}
	// console.log(response);
	// if (response.data.detail.code != 0) {
	// 	Vue.prototype.$message({
	// 		showClose: true,
	// 		message: response.data.detail.message,
	// 		type: 'error'
	// 	});
	// }

	// console.log(response.data)
	// if(typeof response.data == "object"){
	// 	response.data.headers = response.headers;
	// }
	return response
}, function(error) {
	// alert('4'+JSON.stringify(error.response))
	// 对响应错误做点什么
	if (toast != null) {
		toast.close(); //关闭加载动画
	}
	// console.log('--- 401 res error response ---')
	console.log(error.response)
	// Vue.prototype.$toast.fail(error.response);
	// Vue.prototype.$message({
	// 	showClose: true,
	// 	message: JSON.stringify(error),
	// 	type: 'error'
	// });
	Vue.prototype.$alert(JSON.stringify(error.response.data), '接口提示', {
		confirmButtonText: '确定',
		callback: action => {}
	});
	return Promise.reject(error)
})

export default axios
