import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const originalPush = VueRouter.prototype.push;

VueRouter.prototype.push = function push(location) {
	return originalPush.call(this, location).catch(err => err)
}

import login from "../views/login.vue";
import register from "../views/register.vue";
import forget from "../views/forget.vue";
import reset from "../views/reset.vue";

import my from "../views/my/my.vue";
import setUp from "../views/my/child/setUp.vue";
import personal from "../views/my/child/personal.vue";

import examination from "../views/examination/examination.vue";

import query from "../views/query/query.vue";

import study from "../views/study/study.vue";

const routes = [{
		path: "/",
		redirect: '/login'
	}, {
		path: "/login",
		name: "login",
		component: login,
		meta: {
			title: "登录",
			menuShow: false, //导航栏是否显示
			tabbarShow: false, //标签栏是否显示
			titleShow: false, //导航栏标题是否显示
		}
	},
	{
		path: "/register",
		name: "register",
		component: register,
		meta: {
			title: "注册",
			menuShow: true, //导航栏是否显示
			tabbarShow: false, //标签栏是否显示
			titleShow: false, //导航栏标题是否显示
		}
	},
	{
		path: "/forget",
		name: "forget",
		component: forget,
		meta: {
			title: "忘记密码",
			menuShow: true, //导航栏是否显示
			tabbarShow: false, //标签栏是否显示
			titleShow: true, //导航栏标题是否显示
		}
	},
	{
		path: "/reset",
		name: "reset",
		component: reset,
		meta: {
			title: "重置密码",
			menuShow: true, //导航栏是否显示
			tabbarShow: false, //标签栏是否显示
			titleShow: true, //导航栏标题是否显示
		}
	},
	{
		path: "/my",
		name: "my",
		component: my,
		meta: {
			title: "我的",
			menuShow: false, //导航栏是否显示
			tabbarShow: true, //标签栏是否显示
			titleShow: false, //导航栏标题是否显示
		}
	},
	{
		path: "/my/setUp",
		name: "setUp",
		component: setUp,
		meta: {
			title: "设置",
			menuShow: true, //导航栏是否显示
			tabbarShow: false, //标签栏是否显示
			titleShow: true, //导航栏标题是否显示
		}
	},
	{
		path: "/my/personal",
		name: "personal",
		component: personal,
		meta: {
			title: "个人信息",
			menuShow: true, //导航栏是否显示
			tabbarShow: false, //标签栏是否显示
			titleShow: true, //导航栏标题是否显示
		}
	},
	{
		path: "/examination",
		name: "examination",
		component: examination,
		meta: {
			title: "考试",
			menuShow: false, //导航栏是否显示
			tabbarShow: true, //标签栏是否显示
			titleShow: false, //导航栏标题是否显示
		}
	},
	{
		path: "/query",
		name: "query",
		component: query,
		meta: {
			title: "查询",
			menuShow: false, //导航栏是否显示
			tabbarShow: true, //标签栏是否显示
			titleShow: false, //导航栏标题是否显示
		}
	},
	{
		path: "/study",
		name: "study",
		component: study,
		meta: {
			title: "学习",
			menuShow: false, //导航栏是否显示
			tabbarShow: true, //标签栏是否显示
			titleShow: false, //导航栏标题是否显示
		}
	},
];

const router = new VueRouter({
	// mode: 'history',  //去掉url中的#
	routes
});

export default router;
