<template>
	<!-- 忘记密码 -->
	<div class="page-box">
		<div class="center-box">
			<input type="text" v-model="form.email" placeholder="请输入您的邮箱地址" />
			<div class="input-cell">
				<input type="password" v-model="form.code" placeholder="验证码" />
				<span class="link leftText">获取验证码</span>
			</div>
			<van-button type="info" block @click="nextStep">下一步</van-button>
		</div>
	</div>
</template>

<script>
export default {
	name: '',
	data() {
		return {
			form: {
				email: '',
				code: '',
			}
		};
	},
	created() {},
	mounted() {},
	computed: {},
	methods: {
		nextStep(){
			let reg = /\w[-\w.+]*@([A-Za-z0-9][-A-Za-z0-9]+\.)+[A-Za-z]{2,14}/;
			if(this.form.email == ''){
				this.$toast.fail('请填写邮箱');
				return
			}else if(this.form.code == ''){
				this.$toast.fail('请填写验证码');
				return
			}else{
				if(!reg.test(this.form.email)){
					this.$toast.fail('请输入正确的邮箱地址');
					return
				}
			}
			this.$router.push('/reset')
		}
	},
	components: {},
	beforeDestroy() {},
	watch: {}
};
</script>

<style scoped lang="scss">
.page-box {
	width: calc(100% - 30px);
	height: calc(100% - 55px);
	padding: 45px 15px 10px 15px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	background-color: #fff;
	overflow: auto;

	.link {
		color: #169bd5;
	}

	.center-box {
		width: 100%;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: center;

		::v-deep {
			.van-button {
				width: calc(100% - 30px);
				border-radius: 6px;
				background-color: #169bd5;
				margin: 50px 0 30px 0;
			}

			.van-button__text {
				font-size: 18px;
			}
		}

		input {
			width: calc(100% - 5px);
			border-left: none;
			border-right: none;
			border-top: none;
			border-color: #a8a8a8;
			border-width: 1px;
			margin: 50px 0 60px 0;
			padding: 0 0 8px 5px;
			font-size: 15px;
		}

		.input-cell {
			width: 100%;
			border-bottom: 1px solid #a8a8a8;
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin-bottom: 50px;

			input {
				border: none;
				margin: 0;
			}

			.leftText {
				white-space: nowrap;
				font-size: 15px;
				margin-bottom: 6px;
			}
		}
	}
}
</style>
