<template>
	<!-- 重置密码 -->
	<div class="page-box">
		<div class="center-box">
			<div class="tips-input-cell">
				<input type="password" v-model="form.password1" placeholder="新密码" />
				<span class="tipsText">密码要求8-16位，至少包含数字、字母、符号两种元素</span>
			</div>
			<input type="password" v-model="form.password2" placeholder="重复该密码" />
			<van-button type="info" block @click="resetClick">确定</van-button>
		</div>
	</div>
</template>

<script>
export default {
	name: '',
	data() {
		return {
			form: {
				password1: '',
				password2: ''
			}
		};
	},
	created() {},
	mounted() {},
	computed: {},
	methods: {
		resetClick() {
			let pawReg = /^(?!.[^A-Za-z0-9])(?![a-z]*$)(?![A-Z]*$)(?![0-9]*$)(?![\u4E00-\u9FA5]*$).{8,16}$/
			let reg = /\w[-\w.+]*@([A-Za-z0-9][-A-Za-z0-9]+\.)+[A-Za-z]{2,14}/;
			if (this.form.password1 == '') {
				this.$toast.fail('请填写密码');
				return;
			} else if (this.form.password2 == '') {
				this.$toast.fail('请填写重复密码');
				return;
			} else {
				if (this.form.password1 != this.form.password2) {
					this.$toast.fail('两次密码不一致');
					return;
				}else if(!pawReg.test(this.form.password1)){
					this.$toast.fail('密码要求8-16位，至少包含数字、字母、符号两种元素');
					return
				}
			}
			this.$router.push('/login');
		}
	},
	components: {},
	beforeDestroy() {},
	watch: {}
};
</script>

<style scoped lang="scss">
.page-box {
	width: calc(100% - 30px);
	height: calc(100% - 55px);
	padding: 45px 15px 10px 15px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	background-color: #fff;
	overflow: auto;

	.link {
		color: #169bd5;
	}

	.center-box {
		width: 100%;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: center;

		::v-deep {
			.van-button {
				width: calc(100% - 30px);
				border-radius: 6px;
				background-color: #169bd5;
				margin: 50px 0 30px 0;
			}

			.van-button__text {
				font-size: 18px;
			}
		}

		input {
			width: calc(100% - 5px);
			border-left: none;
			border-right: none;
			border-top: none;
			border-color: #a8a8a8;
			border-width: 1px;
			margin: 0px 0 40px 0;
			padding: 0 0 8px 5px;
			font-size: 15px;
		}

		.tips-input-cell {
			width: 100%;
			margin: 60px 0 60px 0;

			input {
				margin: 0 0 5px 0;
			}

			.tipsText {
				font-size: 12px;
				color: #a8a8a8;
				margin-left: 5px;
			}
		}
	}
}
</style>
